export default {
	data() {
		return {
			show: false,
			value: '获取途径',
			currentIndex: 0,
			text_box: [{
				name: '直接获取'
			}, {
				name: '秒杀活动'
			}, {
				name: '竞猜活动'
			}, {
				name: '刮刮活动'
			}, {
				name: '众筹活动'
			}, {
				name: '拼团活动'
			}]
		}
	},
	methods: {
		changeIndex(index) {
			this.currentIndex = index;
			this.value = this.text_box[index].name
			this.show = !this.show
		},
		open_list: function() {
			this.show = !this.show
		}
	}
}
